


















import Vue from 'vue';

export default Vue.extend({
  name: 'MovieDetailsLinks',

  props: {
    imdb: {
      type: String,
    },
    youtube: {
      type: String,
    },
    homepage: {
      type: String,
    },
  },
  methods: {
    goToImdb(): void {
      window.open(`https://www.imdb.com/title/${this.imdb}`, '_blank');
    },
    goToYoutube(): void {
      window.open(`https://www.youtube.com/watch?v=${this.youtube}`, '_blank');
    },
    goToMoviepage(): void {
      window.open(this.homepage, '_blank');
    },
    goBack(): void {
      this.$router.go(-1);
    },
  },
});
